body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*= Card style attributes =*/
.card {
    background: #ff0000;
    border-radius: 2px;
    height: 250px;
    margin: 16px;
    position: relative;
    width: 325px;
    text-align: left;
    box-shadow: 0 3px 6px #000000, 0 3px 6px #000000;
}

/*= Card Image Container Styling attributes =*/
.card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: #ffff00;
}

/*= Card Image Width =*/
.card > .img-container > img {
    width: 60%;
}

/*= Cards content styling attributes =*/
.card > .content {
    padding-left: 16px;
    padding-right: 16px;
    font-size: 15px;
}
/*= Wrapper styling attributes =*/
.wrapper {
    padding-top: 50px;
    background: #ff0000;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
/*= Header styling attributes =*/
.header 
{
    width: 100%;
    font-size: 30px;
    font-weight: bold;
    color: #ffff00;
    margin-bottom: 10px;
}

/*= Title styling attributes =*/
.title 
{
    float: left;
}

/*= Scores styling sttributes =*/ 
.scores {
    float: right; 
}
